import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { Platform } from '@ionic/angular';

import { BasePage } from '../base/base.page';
import { UtilsService } from '../@core/utils.service';
import { Utils } from '../@global/utils';
import { Constants } from 'src/app/@global/constants';

@Component({
  selector: 'app-download-new-version',
  templateUrl: './download-new-version.page.html',
  styleUrls: ['./download-new-version.page.scss'],
})
export class DownloadNewVersionPage extends BasePage implements OnInit {
  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    private platform: Platform
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {}

  public openStore(): void {
    const storeUrl = this.platform.is('android') ? Constants.MARKET_ANDROID_URL : Constants.MARKET_IOS_URL;
    this.utilsService.openLink(storeUrl);
  }
}
